.small-card {
    display: flex;
    width: 100%;


}
.small-card img {
    height: 100%;
    object-fit:cover;
    margin:auto 0 ;
}

.image-container-small{
    margin-right: 20px;
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    overflow: hidden;
    border-radius: 50%;

}


.small-card p  {
    color: #49C5B6;
    font-family: "Lato", sans-serif;
}
.small-card small {
    font-family: "Lato", sans-serif;
}
.small-card-info {
    word-wrap: break-word;
    cursor: pointer;
}
.small-cont {
    margin: 10px;
    border: 1px solid #eee;
    padding:10px 
}
.small-text-expanded {
    text-align: center;
    font-family: "Lato", sans-serif;
    font-weight: 300;
    font-size: 0.8em;
    text-transform: uppercase;

}